<template lang="">
    <!-- <div> -->
        <div class="center">
            
            
<div class="effect" :style="`
            transform-origin : ${diam+70}px ${diam+70}px;
            transform: rotate(${(360/180*i)}deg);
            left : -${diam+70}px; top : -${diam+70}px;                  
            `" v-for="i in 180" :key="i"></div>


            <div class="p" :style="`
            transform-origin : ${diam}px ${diam}px;
            transform: rotate(${45+(360/this.Items.length*i)}deg);
            left : -${diam}px; top : -${diam}px;                  
            `" v-for="(item,i) in Items" :key="i">

            <p :style="`transform: rotate(${-45-(360/this.Items.length*i)}deg);
            position: absolute;`" 
            v-bind:class="{'active':(i === this.On_i)}">
            {{item}}            
            </p>           

            </div>

            
        
        </div>
        
         <!-- left : ${h_diam};top : ${h_diam};  -->

        
        <!-- <div class="white-bg" v-for="item in items" :key="item.id">{{item}}</div> -->
    <!-- </div> -->
</template>
<script>
export default {
    name: 'App',
    data(){
        return{
            // items : ["월","화","수","목","금","토","일"],
            // h_diam : 0,
            // 50,
            h_diam : -(this.diam/2),
            degree: 30,
        }
    },
    props:
    {
        On_i : Number,
        Items: Array,
        diam: Number,
        },
    methods : {
        cssVars() {
            return {
                '--diam': this.diam+"px",
                '--color': this.bgColor,
                
                }
                }
                }
}
</script>
<style scoped>

#App{
    background: transparent;
    background: black;
    }
template
{
    background: black;
}

html,body
{

background: black;
}

.center 
{
    background: black;
    position: fixed;
    top: 50%;
    left: 50%;
    /* transform-origin: center; */
}

.circle{
    border-radius: 50%;
    border:1px solid blue;
    line-height: 200px;
    text-align: center;
    position: absolute;
}
.active
{
    color: black;
}
.effect
{
    position: absolute;
    height: 300px;
    border-top: 1px solid rgba(250, 221, 60, 0.144);
    width: 300px;
    /* width: 1000px; */
    pointer-events: none;
    z-index: -1;
}


.p{
    position: absolute;
    height: 30px;
    /* background: wheat; */
    width: 70px;
    z-index: -1;
}
p{
    color: grey;
    z-index: 5;
    background: white;
}
p:focus {
    outline: none;
    background-color:transparent;
}
p:hover{
    
    /* background: brown; */
    color: blue;
}

</style>